import { EventBroker, Subscription } from '@cdp/event-publisher';

/** @internal */
export const _cancel = Symbol('cancel');
/** @internal */
export const _close = Symbol('close');

/**
 * @en CancelToken state definitions.
 * @ja CancelToken 
 *
 * @internal
 */
export const enum CancelTokenState {
    /**  */
    OPEN        = 0x0,
    /**  */
    REQUESTED   = 0x1,
    /**  */
    CLOSED      = 0x2,
}

/**
 * @en Cancel event definitions.
 * @ja 
 *
 * @internal
 */
export interface CancelEvent<T> {
    cancel: [T];
}

/**
 * @en Internal CancelToken interface.
 * @ja CancelToken 
 *
 * @internal
 */
export interface CancelTokenContext<T extends {}> {
    readonly publisher: EventBroker<CancelEvent<T>>;
    readonly subscriptions: Set<Subscription>;
    reason: T | undefined;
    status: CancelTokenState;
}

/**
 * @en Invalid subscription object declaration.
 * @ja  Subscription 
 *
 * @internal
 */
export const invalidSubscription = Object.freeze({
    enable: false,
    unsubscribe() { /* noop */ }
}) as Subscription;
